.zolo-import-block-modal {
    .components-form-file-upload .components-button {
        border: 1px solid #dddddd;
        width: 100%;
        height: 150px;
        display: flex;
        justify-content: center;
        flex-direction: column-reverse;
        gap: 10px;
        color: #2804ff;
        border-radius: 3px;
        cursor: copy;
    }

    .zolo-export-pattern-btn {
        background: #2804ff;
        color: #fff;
        padding: 10px 20px;
        font-size: 14px;
        border-radius: 3px;
        border: none;
        cursor: pointer;
        margin-top: 24px;
    }
}
